.course-module {
    display: flex;
    min-height: 635px;
    border-bottom: 1px solid rgba(47, 47, 47, 0.15);

    @media (max-width: $screenXXL - 1) {
        min-height: 685px; }

    @media (max-width: $screenL - 1) {
        min-height: 733px; }

    @media (max-width: $screenM - 1) {
        min-height: 747px; }

    @media (max-width: $screenS - 1) {
        min-height: 700px; }

    @media (max-width: $screenXXS - 1) {
        flex-direction: column; }

    a.course-module__read-more {
        display: none;
        font-family: $font-alt;
        font-size: 13px;
        line-height: 19px;
        color: $textColor;
        border-bottom: 1px solid rgba(200, 104, 76, 0.2);
        margin-bottom: 29px;

        .less {
            display: none; }

        .icon {
            padding-left: 4px; }

        &.opened {

            .less {
                display: flex; }

            .more {
                display: none; } }

        @media (max-width: 639px) {
            display: inline-block;
            vertical-align: top; } }

    &__inner {
        display: flex;
        padding-bottom: 80px;

        @media (max-width: 899px) {
            padding-bottom: 54px; }

        @media (max-width: 767px) {
            padding-bottom: 50px; }

        @media (max-width: 639px) {
            flex-direction: column; } }

    &__header {
        min-height: 210px;

        @media (max-width: $screenXXS - 1) {
            padding-right: 50px;
            min-height: 0; } }

    &__info-block {
        display: inline-block;
        vertical-align: top;
        box-sizing: border-box;
        padding-top: 65px;
        padding-bottom: 30px;
        padding-left: 8.333333%;
        padding-right: 40px;
        width: 58.368%;

        @media (max-width: $screenXXL - 1) {
            padding-right: 20px; }

        @media (max-width: $screenL - 1) {
            padding-top: 67px;
            padding-right: 0; }

        @media (max-width: $screenM - 1) {
            padding-top: 35px; }

        @media (max-width: $screenS - 1) {
            padding-left: 33px;
            padding-top: 75px; }

        @media (max-width: $screenXS - 1) {
            padding-bottom: 12px;
            width: 53.4%; }

        @media (max-width: $screenXXS - 1) {
            order: 2;
            padding-top: 0;
            padding-left: 20px;
            width: 100%; } }

    &__image-block {
        display: inline-block;
        vertical-align: top;
        width: 41.632%;
        //background-color: #c4c4c4
        overflow: hidden;

        @media (max-width: $screenL - 1) {
            padding-top: 90px; }

        @media (max-width: $screenM - 1) {
            padding-top: 0; }

        @media (max-width: $screenS - 1) {
            padding-top: 20px; }

        @media (max-width: $screenXS - 1) {
            width: 46.6%; }

        @media (max-width: $screenXXS - 1) {
            order: 1;
            padding-top: 15px;
            width: 100%;
            max-height: 299px;
            overflow: hidden; }

        @media (max-width: $screenMob - 1) {
            padding-top: 0;
            max-height: 280px; } }

    &__image {
        display: block;
        width: auto;
        max-height: 635px;

        @media (max-width: $screenXXL - 1) {
            max-height: 680px; }

        @media (max-width: $screenL - 1) {
            max-height: 530px; }

        @media (max-width: $screenM - 1) {
            max-height: 480px; }

        @media (max-width: $screenS - 1) {
            max-height: 330px; }

        @media (max-width: $screenXXS - 1) {
            max-height: 320px; } }

    &__title {
        margin: 0 0 38px 0;
        font-weight: 400;
        font-size: 49px;
        line-height: 54px;
        letter-spacing: -0.02em;

        @media (max-width: $screenXXL - 1) {
            margin-bottom: 36px; }

        @media (max-width: $screenL - 1) {
            margin-bottom: 25px;
            line-height: 51px;
            letter-spacing: -1.4px; }

        @media (max-width: $screenM - 1) {
            margin-bottom: 16px; }

        @media (max-width: $screenS - 1) {
            margin-bottom: 50px;
            font-size: 29px;
            line-height: 31px; }

        @media (max-width: $screenXS - 1) {
            margin-bottom: 19px; }

        @media (max-width: $screenXXS - 1) {
            margin-bottom: 13px;
            line-height: 32px; }

        @media (max-width: $screenMob - 1) {
            margin-bottom: 21px;
            line-height: 31px; } }

    &__label {
        position: relative;
        display: inline-block;
        vertical-align: top;
        margin: 0;
        font-style: italic;
        color: $brown;
        z-index: -1;

        &:before {
            content: "";
            position: absolute;
            top: 4px;
            left: -27px;
            width: 14px;
            height: 23px;
            background-image: url("../images/svg/flag.svg");
            opacity: 0.2;

            @media (max-width: $screenXXL - 1) {
                top: 10px; }

            @media (max-width: $screenM - 1) {
                left: -40px; }

            @media (max-width: $screenS - 1) {
                left: -4px;
                top: 2px; } }

        @media (max-width: $screenS - 1) {
            padding-left: 30px; } }

    &__body {

        @media (max-width: $screenS - 1) {
            width: 177%; }

        @media (max-width: $screenXS - 1) {
            width: 197%; }

        @media (max-width: $screenXXS - 1) {
            width: 100%; } }

    &__info {
        margin-bottom: 62px;
        display: flex;

        @media (max-width: $screenXXL - 1) {
            margin-bottom: 52px; }

        @media (max-width: $screenL - 1) {
            margin-bottom: 86px; }

        @media (max-width: $screenM - 1) {
            margin-bottom: 89px; }

        @media (max-width: $screenS - 1) {
            margin-bottom: 14px; }

        @media (max-width: $screenXXS - 1) {
            margin-bottom: 26px; }

        @media (max-width: $screenMob - 1) {
            margin-bottom: 20px; } }

    &__info-col {
        display: inline-block;
        vertical-align: top;
        box-sizing: border-box;
        padding: 13px 16px 3px 16px;
        min-width: 208px;
        font-family: $font-alt;
        border-top: 1px solid rgba(47, 47, 47, 0.15);
        border-left: 1px solid rgba(47, 47, 47, 0.15);

        @media (max-width: $screenXXL - 1) {
            padding-top: 11px;
            padding-bottom: 0; }

        @media (max-width: $screenL - 1) {
            padding-left: 13px;
            padding-right: 13px; }

        @media (max-width: $screenS - 1) {
            min-width: 177px; }

        @media (max-width: $screenXS - 1) {
            min-width: 144px; }

        @media (max-width: $screenXXS - 1) {
            padding-top: 10px;
            min-width: 53%; }

        @media (max-width: $screenMob - 1) {
            min-width: 56%; }

        &:first-child {
            padding-left: 0;
            border-left: none; } }

    &__info-col-header {
        margin: 0 0 5px 0;
        font-size: 13px;
        line-height: 17px;
        color: rgba(47, 47, 47, 0.6);

        @media (max-width: $screenXXL - 1) {
            font-size: 14px;
            line-height: 18px; }

        @media (max-width: $screenXXS - 1) {
            margin-bottom: 6; } }

    &__info-col-descr {
        margin: 0;
        font-size: 15px;
        line-height: 19px;

        @media (max-width: $screenXXL - 1) {
            font-size: 17px;
            line-height: 21px; }

        @media (max-width: $screenL - 1) {
            line-height: 19px;
            letter-spacing: -1px; }

        @media (max-width: $screenS - 1) {
            font-size: 13px;
            line-height: 16px;
            letter-spacing: 0.1px; }

        @media (max-width: $screenXXS - 1) {
            font-size: 13px;
            letter-spacing: 0.05px;
            line-height: 15px; } }

    &__title-wrapper {
        padding-left: 8.333333%;
        padding-top: 65px;
        max-width: 670px;

        @media (max-width: $screenXL - 1) {
            padding-left: 76px;
            max-width: 570px; }

        @media (max-width: 899px) {
            padding-top: 28px;
            padding-left: 35px;
            max-width: 357px;
            min-width: 280px; }

        @media (max-width: 767px) {
            max-width: 380px; }

        @media (max-width: 639px) {
            padding-left: 20px;
            max-width: 280px; } }


    &__info-col-img {
        float: left;
        margin-right: 11px;
        width: 59px;
        height: 59px;
        overflow: hidden;

        @media (max-width: $screenL - 1) {
            display: none; }

        img {
            display: block;
            width: 100%;
            height: auto; } }

    &__course-descr {
        margin-top: auto;
        margin-bottom: 32px;
        padding-top: 12px;

        @media (max-width: $screenXL - 1) {
            padding-top: 14px;
            padding-right: 73px; }

        @media (max-width: 899px) {
            padding-right: 36px; }

        @media (max-width: 767px) {
            padding-right: 33px; }

        @media (max-width: 639px) {
            position: relative;
            margin-bottom: 4px;
            max-height: 180px;
            overflow: hidden;
            transition: max-height 0.3s ease;

            &.full {
                max-height: 2000px;

                &:after {
                    display: none; } }

            &:after {
                content: "";
                position: absolute;
                bottom: 0;
                left: 0;
                right: 0;
                height: 50px;
                background-image: linear-gradient(to top, #fff, $transparent); } }

        p {
            margin: 0;
            padding-right: 16.666666%;
            //max-width: 585px
            font-size: 20px;
            line-height: 26px;

            @media (max-width: 899px) {
                font-size: 15px;
                line-height: 19px; } }

        .cur {
            font-style: italic; }

        a {
            color: $brown; } }

    &--extended {
        flex-direction: column;

        .course-module__info-block {
            padding: 0;
            width: 50%;

            @media (max-width: 639px) {
                width: 100%; }

            &:nth-child(2) {
                display: flex;
                flex-direction: column;
                padding-top: 11px;

                @media (max-width: $screenXL - 1) {
                    padding-top: 30px; }

                @media (max-width: 899px) {
                    padding-top: 0;
                    margin-top: -48px; }

                @media (max-width: 767px) {
                    margin-top: 0;
                    padding-top: 18px; }

                @media (max-width: 639px) {
                    padding-top: 45px;
                    padding-left: 20px;
                    padding-right: 20px; } } }


        .course-module__header {
            padding-left: 16.666666%;
            padding-top: 10px;
            min-height: 99px;

            @media (max-width: $screenXL - 1) {
                padding-top: 31px;
                padding-left: 75px; }

            @media (max-width: 899px) {
                padding-top: 19px;
                padding-left: 35px; }

            @media (max-width: 767px) {
                padding-top: 16px; }

            @media (max-width: 639px) {
                padding-left: 20px;
                padding-right: 20px; } }

        .course-module__info {
            margin-bottom: 0; }

        .course-module__title {
            font-size: 54px;
            line-height: 57px;

            @media (max-width: 899px) {
                font-size: 29px;
                line-height: 33px;
                letter-spacing: -0.5px;
                margin-bottom: 0; } }

        .course-module__label {
            font-size: 52px;

            @media (max-width: 899px) {
                font-size: 29px;
                line-height: 33px;
                padding-left: 24px;
                letter-spacing: 0.1px; }

            &:before {
                left: -37px;
                width: 16px;
                height: 30px;
                opacity: 1;
                background-image: url("../images/svg/flag-red.svg");

                @media (max-width: 899px) {
                    left: 1px;
                    top: 7px;
                    width: 14px;
                    height: 22px;
                    background-image: url("../images/svg/flag.svg");
                    opacity: 0.2; } } }

        .course-module__info-col {
            width: 45.5%;

            @media (max-width: $screenXL - 1) {
                padding-left: 16px;
                width: 43.7%;
                min-width: 100px;

                @media (max-width: 767px) {
                    width: 46%; }

                &:first-child {
                    padding-left: 0; } }

            @media (max-width: 899px) {
                width: 48%; }

            @media (max-width: 767px) {
                width: 46%; }

            @media (max-width: 639px) {
                width: 50%; } }

        .course-module__info-col-descr {

            @media (max-width: $screenXL - 1) {
                min-height: 38px; } }

        .course-module__body {

            @media (max-width: 899px) {
                width: 100%; } }

        .course-module__image-block {
            box-sizing: border-box;
            display: flex;
            justify-content: flex-end;
            padding-right: 172px;
            padding-top: 56px;
            width: 100%;

            @media (max-width: $screenXL - 1) {
                padding-top: 66px;
                padding-right: 124px; }

            @media (max-width: 899px) {
                padding-top: 42px;
                padding-right: 49px; }

            @media (max-width: 767px) {
                padding-top: 32px; }

            @media (max-width: 639px) {
                max-height: 292px; }

            img {
                //margin-left: -36px
                //margin-left: -5%
                height: 536px;
                width: 577px;
                min-width: 577px;

                @media (max-width: $screenXL - 1) {
                    max-height: 371px;
                    width: 400px;
                    min-width: 400px; }

                @media (max-width: 899px) {
                    max-height: 379px;
                    width: 410px;
                    min-width: 410px; }

                @media (max-width: 767px) {
                    max-height: 259px;
                    min-width: 282px;
                    width: 282px; } } }

        .social-block {

            @media (max-width: $screenXL - 1) {
                position: relative;
                right: auto;
                bottom: auto; } }

        .social-btn._active .social-btn__actions {

            @media (max-width: $screenXL - 1) {
                min-width: 48px; }

            @media (max-width: 899px) {
                min-width: 56px; }

            @media (max-width: 767px) {
                min-width: 36px; }

            @media (max-width: 639px) {
                min-width: 34px; } }

        .social-btn {
            margin-right: 7px; }

        .book-card {

            &._desktop {

                @media (max-width: 899px) {
                    display: none; } }

            &._mobile {
                display: none;

                @media (max-width: 899px) {
                    display: block;
                    max-width: 350px; }

                @media (max-width: 767px) {
                    max-width: 286px; } } } } }
