.btn {
    display: inline-block;
    vertical-align: top;
    box-sizing: border-box;
    padding: 16px 28px;
    text-align: center;
    font-family: $font;
    font-weight: 300;
    font-size: 16px;
    line-height: 25px;
    color: #fff;
    background-color: $transparent;
    border: none;
    border-radius: 0;
    outline: none;
    transition: opacity 0.4s ease;
    cursor: pointer;

    &:hover,
    &:focus,
    &:active {
        opacity: 0.9; }

    &--gray {
        @include hover;
        padding: 14px 23px;
        font-family: $font-alt;
        font-weight: 700;
        font-size: 18px;
        line-height: 20px;
        color: $textColor;
        border: 1px solid $textColor-l;

        @media (max-width: 899px) {
            font-size: 14px;
            padding: 12px; }

        .price {
            color: $brown;
            @include numeric; } } }
