.page-header {
    background-color: #fff;

    &__menu-mobile {

        @media (max-width: $screenS - 1) {
            position: relative;
            display: flex;
            box-sizing: border-box;
            padding: 18px;
            padding-top: 16px;
            padding-bottom: 10px;
            height: 64px;
            transition: background-color 0.3s ease; }

        &:after {

            @media (max-width: $screenS - 1) {
                content: "";
                position: absolute;
                bottom: 0;
                left: 18px;
                right: 18px;
                height: 1px;
                background-color: rgba(47, 47, 47, 0.2); } } }

    &.opened {

        .page-header__menu-mobile {

            @media (max-width: $screenS - 1) {
                background-color: $textColor;

                &:after {
                    background-color: rgba(255, 255, 255, 0.2); } } } }

    @media (max-width: $screenS - 1) {
        padding-bottom: 0;
        border-bottom: none; }

    &._fixed {
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        background-color: #fff;
        z-index: 50;
        transition: transform 0.3s ease; }

    &._sticky {
        position: sticky;
        top: 0;
        left: 0;
        right: 0;
        background-color: #fff;
        z-index: 50;
        transition: transform 0.3s ease; }

    &._animate {
        transform: translateY(-100%); }

    &._animate_rev {
        transform: translateY(0); }

    &__row {
        position: relative;
        height: 94px;
        border-bottom: 1px solid rgba(47, 47, 47, 0.15);

        @media (max-width: $screenS - 1) {
            height: auto;
            border-bottom: none; } }

    &__wrapper {
        display: flex;
        box-sizing: border-box;
        padding: 27px 0 27px 65px;
        max-width: 100%; } }

.logo {
    display: block;
    margin-right: 44px;

    @media (max-width: $screenS - 1) {
        display: none; }

    svg {
        display: block; } }

.logo-mobile {
    display: none;
    margin-right: 16px;

    @media (max-width: $screenS - 1) {
        display: block; }

    svg {
        display: block;
        fill: $textColor;

        .opened & {
            fill: #fff; } } }
