.lectures-menu-row {
    height: 53px;
    position: fixed;
    left: 0;
    right: 0;
    background-color: #fff;

    @media (max-width: $screenS - 1) {
        border-bottom: 1px solid $lines-d;

        ._dark & {
            border-bottom: 1px solid rgba(255,255,255,0.2); } } }

.lectures-menu {
    position: relative;
    @include row-component;
    padding-left: 16px;
    padding-right: 20px;
    transition: background-color 0.3s ease;

    @media (max-width: $screenL - 1) {
        padding-right: 0; }

    @media (max-width: $screenXS - 1) {
        padding-left: 8px; }

    &:after {
        content: "";
        position: absolute;
        bottom: 0;
        left: 93px;
        right: 0;
        border-bottom: 1px solid rgba(255, 255, 255, 0.2);

        @media (max-width: $screenXS - 1) {
            left: 0; } }

    &._dark {
        color: #fff;

        &.opened {
            background-color: $textColor; } }

    &.opened {

        .lectures-list-wrapper {
            opacity: 1;
            z-index: 1;
            visibility: visible;

            @media (max-width: $screenM - 1) {
                z-index: 2; } } }

    &__section {
        display: flex;
        box-sizing: border-box;
        width: 50%;
        border-left: 1px solid rgba(47, 47, 47, 0.2);

        ._dark & {
            border-left: 1px solid rgba(255, 255, 255, 0.2); }

        &:first-child {
            border-left: none;
            min-width: 50%; } }

    &__link-back {
        display: flex;
        align-items: center;
        margin-left: 2px;
        padding: 16px 0;
        font-family: $font-alt;
        font-size: 14px;
        line-height: 21px;
        color: $textColor;
        transition: opacity 0.3s ease;
        letter-spacing: 0.15px;

        @media (max-width: $screenM - 1) {
            max-width: calc(100% - 75px); }

        @media (max-width: $screenXS - 1) {
            flex-grow: 1;
            margin-left: 22px; }

        ._dark & {
            color: #fff; }

        .icon {
            width: 18px;
            height: 18px;

            svg {
                display: block;
                width: 100%;
                height: 100%;
                fill: $textColor;

                ._dark & {
                    fill: #fff; } } }

        span {

            @media (max-width: $screenXS - 1) {
                font-size: 0; }

            @media (max-width: $screenM - 1) {
                max-width: calc(100% - 30px);
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap; } }

        .label {
            margin-left: 9px;
            color: $brown; }

        &:hover {
            opacity: 0.8; } }

    .play-block {

        @media (max-width: $screenXS-s - 1) {
            display: none; } } }

.logo-min {
    display: block;
    margin-top: 14px;
    margin-bottom: 0;
    width: 75px;
    min-width: 75px;
    height: 40px;

    @media (max-width: $screenXS - 1) {
        margin-top: 9px;
        width: 57px;
        min-width: 57px;
        height: 30px; }

    svg {
        display: block;
        width: 100%;
        height: 100%;

        ._dark & {
            fill: #fff; } } }

.play-block-mobile {
    display: none;
    margin-left: auto;
    width: 36px;

    &__play-block {
        flex-direction: column; }

    &__duration {
        font-family: $font-alt;
        font-size: 14px;
        line-height: 20px;
        color: $textColor-l;

        ._dark & {
            color: rgba(255, 255, 255, 0.6); }

        .active & {
            color: $textColor-l; } }

    @media (max-width: $screenXS-s - 1) {
        display: block; } }
