.link-to-transcript {
    position: absolute;
    top: 50%;
    right: 20px;
    transform: translateY(-50%);
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 122px;
    font-family: $font-alt;
    font-size: 14px;
    line-height: 20px;
    color: #fff;
    transition: opacity 0.3s ease;
    z-index: 1;

    @media (max-width: $screenM - 1) {
        right: 8px;
        top: auto;
        bottom: 115px;
        transform: translateY(0);
        width: 200px;
        padding-right: 36px;
        text-align: right;

        br {
            display: none; } }

    @media (max-width: $screenXS - 1) {
        bottom: 55px;
        right: 0; }

    @media (max-width: $screenXS-s - 1) {
        right: 14px;
        left: 21px;
        bottom: 33px;
        width: auto;
        text-align: left; }


    &:after {
        content: "";
        position: absolute;
        top: 50%;
        right: 15px;
        transform: translateY(-50%) rotate(45deg);
        width: 83px;
        height: 83px;
        border: 3px solid #fff;
        border-bottom: none;
        border-left: none;

        @media (max-width: $screenM - 1) {
            width: 15px;
            height: 15px; } }

    &:hover {
        opacity: 0.6; } }
